<template>
  <div class="container">
    <el-row justify="center" type="flex">
      <el-tooltip
        class="item"
        effect="dark"
        content="Editar contenido de pagina"
        placement="top"
      >
        <el-button
          type="warning"
          icon="el-icon-edit-outline"
          plain
          class="button-size"
          @click.stop.prevent="goToContent"
          >Contenido</el-button
        >
      </el-tooltip>
      <el-tooltip
        class="item"
        effect="dark"
        content="Previsualizar pagina"
        placement="top"
      >
        <el-button
          type="success"
          icon="el-icon-view"
          plain
          class="button-size"
          @click.stop.prevent="goToPreview"
          >Previsualizar</el-button
        >
      </el-tooltip>
    </el-row>
    <el-row class="container-button" justify="center" type="flex">
      <el-tooltip
        class="item"
        effect="dark"
        content="Editar opciones de pagina"
        placement="top"
      >
        <el-button
          type="primary"
          icon="el-icon-edit"
          plain
          class="button-size"
          @click.stop.prevent="editNode"
          >Editar</el-button
        >
      </el-tooltip>
      <el-tooltip
        class="item"
        effect="dark"
        content="Configuraciones de pagina (archivos js y css)"
        placement="top"
      >
        <el-button
          type="danger"
          icon="el-icon-setting"
          plain
          class="button-size"
          @click="goToConfigs"
          >Configuración</el-button
        >
      </el-tooltip>
    </el-row>
  </div>
</template>

<script>
import { useStorePage } from '../../../stores/page/pageStore'
import { useStoreConfig } from '../../../stores/configStore'
import _ from 'lodash'

const pageStore = useStorePage()
const configStore = useStoreConfig()

export default {
  name: 'menuButtons',
  data () {
    return {
      pageStore,
      configStore
    }
  },
  methods: {
    goToConfigs () {
      pageStore.dialogVisible = false
      this.$router.push({
        name: 'pages.edit.config',
        params: { keyName: pageStore.data.keyNamePage }
      })
    },
    goToEdit () {
      this.$router.push({
        name: 'pages.edit'
        // params: { keyName: row.keyName, typeTemplate: 'main' }
      })
      pageStore.dialogVisible = false
    },
    async goToContent () {
      // console.log('~ data', this.data)
      this.$router.push({
        name: 'pages.edit.content',
        params: { keyName: pageStore.data.keyNamePage, slug: pageStore.data.slug }
      })
      pageStore.dialogVisible = false
    },
    goToPreview () {
      pageStore.dialogVisible = false
      const lang = _.cloneDeep(configStore.langSelected)
      var data = pageStore.node
      var slugs = []
      slugs.push(data.data.slug)
      var slugComplete = ''
      if (data.level > 1) {
        var parent = data.parent
        var level = data.level
        while (level > 0) {
          slugs.push(parent.data.slug)
          parent = parent.parent
          level = parent.level
        }
      }
      slugs.pop()
      slugs = slugs.reverse()
      slugComplete = slugs.join('/')
      slugComplete = '/' + slugComplete
      window.open(configStore.baseUrl + lang + slugComplete, '_blank')
      /* var parent = pageStore.node.parent
      // console.log(pageStore.formNode.slugComplete)
      var slug = '/' + pageStore.data.slug

      while (parent.level >= 1) {
        slug = '/' + parent.data.slug + slug
        parent = parent.parent
      }

      window.open(configStore.baseUrl + slug.substring(1, slug.length), '_blank') */
    },
    editNode () {
      var node = pageStore.node
      var slugComplete = ''
      var flag = false
      if (node.level > 1) {
        var parent = node.parent
        var level = node.level
        if (level > 2) flag = true
        while (level > 0) {
          slugComplete = parent.data.slug + slugComplete
          parent = parent.parent
          level = parent.level
        }
        if (flag) slugComplete = slugComplete + '/'
      }
      pageStore.formNode = pageStore.data
      pageStore.formNode.isEdit = false
      pageStore.formNode.slugComplete = slugComplete
      pageStore.isFormVisible = true
      pageStore.dialogVisible = false
    }
  }
}
</script>

<style lang="scss" scoped>
.button-size {
  width: 9rem;
  height: 3rem;
  // margin-left: 3rem;
  margin-bottom: 1rem;
}
</style>
