<template>
  <div>
    <ValidationObserver ref="form" v-slot="{ invalid }">
      <el-form
        label-position="top"
        label-width="120px"
        :model="formPage"
      >
        <el-form-item label="Título" placeholder="Título pagina">
          <ValidationProvider
            name="título página"
            rules="required"
            v-slot="{ errors }"
          >
            <el-input v-model="formPage.menuTitle"></el-input>
            <span>{{ errors[0] }}</span>
          </ValidationProvider>
        </el-form-item>

        <el-form-item label="keyName" placeholder="keyName">
          <ValidationProvider
            name="keyName"
            rules="uniqueKeyNamePage|required"
            v-slot="{ errors }"
          >
            <el-input v-model="formPage.keyName"></el-input>
            <span>{{ errors[0] }}</span>
          </ValidationProvider>
        </el-form-item>

        <el-form-item>
          <el-button
            type="primary"
            style="float: right"
            @click="createPage()"
            :disabled="invalid"
            >Crear</el-button
          >
        </el-form-item>
      </el-form>
    </ValidationObserver>
  </div>
</template>

<script>
import { useStorePage } from '../../../stores/page/pageStore'
import { useStoreConfig } from '../../../stores/configStore'
import _ from 'lodash'

const pageStore = useStorePage()
const configStore = useStoreConfig()

export default {
  name: 'form-page',
  data () {
    return {
      pageStore,
      configStore,
      formPage: {}
    }
  },
  created () {
    this.formPage = _.cloneDeep(pageStore.templatePage)
  },
  methods: {
    async createPage () {
      pageStore.loading = true

      this.formPage.lang = configStore.langSelected
      pageStore.formNode = _.cloneDeep(pageStore.templateNode)
      pageStore.formNode.isEdit = true

      await pageStore
        .storePage(this.formPage)
        .then((response) => {
          pageStore.formNode.keyNamePage = _.cloneDeep(response.keyName)
          pageStore.formNode.menuTitle = _.cloneDeep(
            this.formPage.menuTitle
          )
        })
        .finally(() => (pageStore.loading = false))

      pageStore.dialogVisibleNode = false
      pageStore.isFormVisible = true
    }
  }
}
</script>
