import request from '@/utils/request'

export function index () {
  return request({
    url: 'site-map',
    method: 'get'
  })
}

export function show (lang) {
  return request({
    url: 'site-map/' + lang,
    method: 'get'
  })
}

export function update (data, lang) {
  return request({
    url: 'site-map/' + lang,
    method: 'put',
    data
  })
}

export function store (data) {
  return request({
    url: 'site-map',
    method: 'post',
    data
  })
}

export function destroy (lang) {
  return request({
    url: 'site-map/' + lang,
    method: 'delete'
  })
}
