<template>
  <div class="app-container">
    <el-row
      v-loading="pageStore.loading"
      element-loading-text="Cargando..."
      :gutter="10"
      type="flex"
      justify="start"
    >
      <!-- Dialog menu -->
      <el-dialog
        :title="'Configuraciones'"
        :visible.sync="pageStore.dialogVisible"
        width="30%"
        :before-close="pageStore.handleClose"
      >
        <menu-buttons />

        <span slot="footer" class="dialog-footer">
          <el-button type="warning" @click="pageStore.dialogVisible = false"
            >Cerrar</el-button
          >
        </span>
      </el-dialog>
      <!-- End Dialog Menu -->

      <!-- Dialog Form Page -->
      <el-dialog
        :title="pageStore.isSubNode ? 'Crear Interior' : 'Crear Pagina'"
        :visible.sync="pageStore.dialogVisibleNode"
        width="30%"
        :before-close="pageStore.handleClose"
      >
        <form-page />
      </el-dialog>
      <!-- End Dialog Form Page -->

      <!-- Form Control Pages -->
      <el-col :span="pageStore.isFormVisible ? 12 : 15">
        <el-card class="box-card">
          <div slot="header" class="clearfix">
            <h3 style="display: inline-flex">
              <strong> Paginas </strong>
            </h3>

            <el-button
              style="float: right; margin-left: 7px"
              type="success"
              icon="el-icon-plus"
              @click="() => seeFormPage()"
              >Nuevo
            </el-button>

            <el-select
              v-model="configStore.langSelected"
              style="float: right"
              placeholder="Idioma"
              @change="onChangeLang"
            >
              <el-option
                v-for="item in configStore.langs"
                :key="item.value"
                :label="item.label"
                :value="item.value"
              >
              </el-option>
            </el-select>
          </div>
          <div class="text item">
            <!-- Tree -->
            <el-tree
              :data="pageStore.list"
              node-key="keyNamePage"
              default-expand-all
              :expand-on-click-node="false"
              @node-click="doubleClick"
              :props="defaultProps"
              draggable
            >
              <span class="custom-tree-node" slot-scope="{ node, data }">
                <span>{{ node.label }}</span>
                <span>
                  <el-tooltip
                    class="item"
                    effect="dark"
                    :content="
                      node.level >= 4
                        ? 'limite de paginas superado'
                        : 'Agregar una pagina interior'
                    "
                    placement="top"
                  >
                    <el-link
                      type="success"
                      icon="el-icon-circle-plus-outline"
                      @click="() => seeFormInternalPage(node, data)"
                      :disabled="node.level >= 4 ? true : false"
                      class="link-button"
                    ></el-link>
                  </el-tooltip>

                  <el-tooltip
                    class="item"
                    effect="dark"
                    content="Configuraciones"
                    placement="top"
                  >
                    <el-link
                      type="primary"
                      icon="el-icon-setting"
                      @click="() => viewMenuSettings(node, data)"
                      class="link-button"
                    ></el-link>
                  </el-tooltip>

                  <el-tooltip
                    class="item"
                    effect="dark"
                    content="Eliminar"
                    placement="top"
                  >
                    <el-link
                      icon="el-icon-delete"
                      class="link-button"
                      type="danger"
                      @click="() => removeNode(node, data)"
                    ></el-link>
                  </el-tooltip>
                </span>
              </span>
            </el-tree>
            <!-- End Tree -->

            <el-divider></el-divider>

            <!-- Button Section -->
            <el-row
              element-loading-text="Cargando..."
              :gutter="20"
              type="flex"
              justify="end"
            >
              <!-- <el-col style="display: flex; justify-content: center" :span="8">
                <el-button
                  @click.stop.prevent="onFormCancel"
                  type="danger"
                  icon="el-icon-back"
                >
                  Cancelar</el-button
                >
              </el-col> -->

              <el-col style="display: flex; justify-content: center" :span="8">
                <el-button
                  @click.stop.prevent="onSaveTree"
                  type="primary"
                  icon="el-icon-circle-plus-outline"
                >
                  Guardar</el-button
                >
              </el-col>
            </el-row>
            <!-- End Button Section -->
          </div>
        </el-card>
      </el-col>
      <!-- Form Create Node -->
      <el-col v-if="pageStore.isFormVisible" :span="12">
        <form-node />
      </el-col>
      <!-- End Form Create Node -->
    </el-row>
  </div>
</template>

<script>
import { useStoreSite } from '../../stores/siteStores'
import { useStoreConfig } from '../../stores/configStore'
import { useStorePage } from '../../stores/page/pageStore'
import { useStoreTemplate } from '../../stores/template/templateStore'
import { useStoreSiteMap } from '../../stores/page/siteMapStore'

import menuButtons from '../pages/components/menuButtons.vue'
import formNode from './components/formNode.vue'
import formPage from './components/formPage.vue'

import _ from 'lodash'

const pageStore = useStorePage()
const siteStore = useStoreSite()
const configStore = useStoreConfig()
const templateStore = useStoreTemplate()
const siteMapStore = useStoreSiteMap()

export default {
  components: {
    menuButtons,
    formNode,
    formPage
  },
  data () {
    return {
      configStore,
      pageStore,
      siteStore,
      templateStore,
      siteMapStore,
      form: null,
      node: null,
      data: null,
      list: [],
      lastNode: 0,
      siteMaps: null,
      defaultProps: {
        label: 'menuTitle',
        children: 'children'
      }
    }
  },
  async created () {
    pageStore.isFormVisible = false
    const lang = _.cloneDeep(configStore.langSelected)
    await siteMapStore.showSiteMap(lang).then((response) => {
      if (response !== undefined && response.map !== undefined) {
        pageStore.list = response.map
        // console.log(pageStore.list)
      }
    })

    this.fetchTemplates()
  },
  methods: {
    async fetchTemplates () {
      templateStore.typeTemplate = 'main'
      const filterFormMain = _.cloneDeep(templateStore.filterForm)
      const typeMain = _.cloneDeep(templateStore.typeTemplate)
      await templateStore
        .indexTemplate(filterFormMain, typeMain)
        .then((response) => {
          pageStore.mainOptions = response
        })

      templateStore.typeTemplate = 'header'
      const filterFormHeader = _.cloneDeep(templateStore.filterForm)
      const typeHeader = _.cloneDeep(templateStore.typeTemplate)
      await templateStore
        .indexTemplate(filterFormHeader, typeHeader)
        .then((response) => {
          pageStore.headerOptions = response
        })

      templateStore.typeTemplate = 'footer'
      const filterFormFooter = _.cloneDeep(templateStore.filterForm)
      const typeFooter = _.cloneDeep(templateStore.typeTemplate)
      await templateStore
        .indexTemplate(filterFormFooter, typeFooter)
        .then((response) => {
          pageStore.footerOptions = response
        })
    },

    seeFormPage () {
      pageStore.isSubNode = false
      pageStore.dialogVisibleNode = true
      pageStore.formPage = _.cloneDeep(pageStore.templatePage)
    },
    seeFormInternalPage (node, data) {
      pageStore.isSubNode = true
      pageStore.dialogVisibleNode = true
      pageStore.formPage = _.cloneDeep(pageStore.templatePage)
      pageStore.data = data
      pageStore.node = node
    },
    removeNode (node, data) {
      this.$confirm('Se borrara la plantilla, ¿Esta seguro?', 'Cuidado', {
        confirmButtonText: 'OK',
        cancelButtonText: 'Cancelar',
        type: 'warning'
      })
        .then(() => {
          pageStore.loading = true
          const parent = node.parent
          const children = parent.data.children || parent.data
          const keyNamePage = _.cloneDeep(data.keyNamePage)
          const index = children.findIndex((d) => d.keyNamePage === data.keyNamePage)
          children.splice(index, 1)

          const map = _.cloneDeep(pageStore.list)
          const lang = _.cloneDeep(configStore.langSelected)

          siteMapStore
            .updateSiteMap(map, lang)
            .then((response) => {
              pageStore.list = response.map

              pageStore.deletePage(keyNamePage)

              pageStore.isFormVisible = false
            })
            .finally(() => (pageStore.loading = false))
        })
        .catch(() => {
          this.$message({
            showClose: true,
            type: 'info',
            message: 'Se cancelo la operación'
          })
        })
    },
    async onChangeLang (lang) {
      configStore.setLangSelected(lang)

      await siteMapStore.indexSiteMap().then(response => {
        pageStore.list = response
      })

      pageStore.list.some(element => {
        if (element.lang === configStore.langSelected) {
          pageStore.list = element.map
          return true
        } else {
          pageStore.list = []
          return false
        }
      })
    },
    viewMenuSettings (node, data) {
      pageStore.dialogVisible = true
      pageStore.node = node
      pageStore.data = data
    },
    doubleClick (node, data) {
      var slugComplete = ''
      var flag = false
      if (data.level > 1) {
        var parent = data.parent
        var level = data.level
        if (level > 2) flag = true
        while (level > 0) {
          slugComplete = parent.data.slug + slugComplete
          parent = parent.parent
          level = parent.level
        }
        if (flag) slugComplete = slugComplete + '/'
      }
      if (!data.checked) {
        data.checked = true
        this.lastNode = data.id
      } else if (data.id === this.lastNode && !pageStore.dialogVisible) {
        pageStore.data = node
        pageStore.node = data
        pageStore.formNode = pageStore.data
        pageStore.formNode.isEdit = false
        pageStore.formNode.slugComplete = slugComplete
        pageStore.isFormVisible = true
        pageStore.dialogVisible = false
        data.checked = false
        this.lastNode = 0
      } else {
        this.lastNode = data.id
      }
    },
    async onSaveTree () {
      pageStore.loading = true
      const map = _.cloneDeep(pageStore.list)
      const lang = _.cloneDeep(configStore.langSelected)

      await siteMapStore
        .updateSiteMap(map, lang)
        .then((response) => {
          pageStore.list = response.map
        })
        .finally(() => (pageStore.loading = false))
    }
  }
}
</script>
<style lang="scss" scoped>
.custom-tree-node {
  flex: 1;
  display: flex;
  align-items: center;
  justify-content: space-between;
  font-size: 15px;
  padding-right: 8px;
}
.link-button {
  margin-right: 1rem;
  font-size: 1.2rem;
}
</style>
