import { defineStore } from 'pinia'
import { Message } from 'element-ui'
// import _ from 'lodash'

import {
  index as indexSiteMap,
  show as showSiteMap,
  store as storeSiteMap,
  update as updateSiteMap
  // destroy as deleteSiteMap
  // getPage
  // updateContent
} from '@/api/pages/siteMap/index'

import { useStoreConfig } from '../configStore'
import { useStorePage } from './pageStore'

const configStore = useStoreConfig()
const pageStore = useStorePage()

export const useStoreSiteMap = defineStore('site-map', {
  state: () => ({
    configStore,
    pageStore,
    list: null,
    siteMaps: null,
    form: {},
    templateSiteMap: {
      keyNameSite: '',
      lang: ''
    }
  }),
  actions: {
    async indexSiteMap () {
      pageStore.loading = true
      return await indexSiteMap()
        .then((response) => {
          this.siteMaps = response.data

          return this.siteMaps
        })
        .catch((e) => {
          console.log('responseCatch -> data', e)
        })
        .finally(() => (pageStore.loading = false))
    },
    async showSiteMap (lang) {
      pageStore.loading = true
      return await showSiteMap(lang)
        .then((response) => {
          this.list = response.data

          return this.list
        })
        .catch((e) => {
          console.log('responseCatch -> data', e)
        })
        .finally(() => (pageStore.loading = false))
    },
    async storeSiteMap (siteMapData) {
      return await storeSiteMap(siteMapData)
        .then((response) => {
          if (!response.success) {
            Message({
              showClose: true,
              message: response.messages[0] || 'Error',
              type: 'danger'
            })
            return false
          }
          Message({
            showClose: true,
            message: response.messages[0] || 'Error',
            type: 'success'
          })
          this.list = response.data

          return this.list
        })
        .catch((e) => {
          console.log('responseCatch -> data', e)
        })
    },
    async updateSiteMap (map, lang) {
      return await updateSiteMap({ map: map }, lang)
        .then((response) => {
          if (!response.success) {
            Message({
              showClose: true,
              message: response.messages[0] || 'Error',
              type: 'danger'
            })
            return false
          }
          Message({
            showClose: true,
            message: response.messages[0] || 'Error',
            type: 'success'
          })

          this.list = response.data

          return this.list
        })
        .catch((e) => {
          console.log('responseCatch -> data', e)
        })
    }
  }
})
