<template>
  <div>
    <el-card class="box-card">
      <div class="text item">
        <!-- Form -->
        <ValidationObserver ref="form" v-slot="{ invalid }">
          <el-form
            label-position="top"
            label-width="120px"
            :model="pageStore.formNode"
          >
            <el-form-item label="Título" placeholder="Título pagina">
              <ValidationProvider
                name="título página"
                rules="required"
                v-slot="{ errors }"
              >
                <el-input v-model="pageStore.formNode.menuTitle"></el-input>
                <span>{{ errors[0] }}</span>
              </ValidationProvider>
            </el-form-item>

            <el-form-item label="keyName" placeholder="keyName">
              <ValidationProvider
                name="keyName"
                rules="required"
                v-slot="{ errors }"
              >
                <el-input v-model="pageStore.formNode.keyNamePage"></el-input>
                <span>{{ errors[0] }}</span>
              </ValidationProvider>
            </el-form-item>

            <el-form-item label="Slug">
              <ValidationProvider
                name="slug"
                rules="required"
                v-slot="{ errors }"
              >
                <el-input placeholder="slug" v-model="pageStore.formNode.slug">
                  <template slot="prepend">{{ configStore.baseUrl.slice(0, -1) + pageStore.formNode.slugComplete }}</template>
                </el-input>
                <span>{{ errors[0] }}</span>
              </ValidationProvider>
            </el-form-item>

            <el-form-item label="Plantilla Header">
              <ValidationProvider
                name="plantilla header"
                rules="required"
                v-slot="{ errors }"
              >
                <el-select
                  v-model="pageStore.formNode.keyNameTemplateHeader"
                  filterable
                  placeholder="Seleccionar plantilla Header"
                >
                  <el-option
                    v-for="item in pageStore.headerOptions"
                    :key="item.keyName"
                    :label="item.title"
                    :value="item.keyName"
                  >
                  </el-option>
                </el-select>
                <span>{{ errors[0] }}</span>
              </ValidationProvider>
            </el-form-item>

            <el-form-item label="Plantilla Main">
              <ValidationProvider
                name="plantilla main"
                rules="required"
                v-slot="{ errors }"
              >
                <el-select
                  v-model="pageStore.formNode.keyNameTemplateMain"
                  filterable
                  placeholder="Seleccionar plantilla Main"
                >
                  <el-option
                    v-for="item in pageStore.mainOptions"
                    :key="item.keyName"
                    :label="item.title"
                    :value="item.keyName"
                  >
                  </el-option>
                </el-select>
                <span>{{ errors[0] }}</span>
              </ValidationProvider>
            </el-form-item>

            <el-form-item label="Plantilla Footer">
              <ValidationProvider
                name="plantilla footer"
                rules="required"
                v-slot="{ errors }"
              >
                <el-select
                  v-model="pageStore.formNode.keyNameTemplateFooter"
                  filterable
                  placeholder="Seleccionar plantilla Footer"
                >
                  <el-option
                    v-for="item in pageStore.footerOptions"
                    :key="item.keyName"
                    :label="item.title"
                    :value="item.keyName"
                  >
                  </el-option>
                </el-select>
                <span>{{ errors[0] }}</span>
              </ValidationProvider>
            </el-form-item>

            <el-form-item label="Clase" placeholder="Clase para body">
              <ValidationProvider
                name="clases"
                rules="required"
                v-slot="{ errors }"
              >
                <el-input v-model="pageStore.formNode.clase"></el-input>
                <span>{{ errors[0] }}</span>
              </ValidationProvider>
            </el-form-item>

            <el-form-item label="Tipo">
              <ValidationProvider
                name="tipo"
                rules="required"
                v-slot="{ errors }"
              >
                <el-select
                  v-model="pageStore.formNode.type"
                  filterable
                  placeholder="Seleccionar tipo"
                >
                  <el-option
                    v-for="item in pageStore.typeOptions"
                    :key="item.value"
                    :label="item.label"
                    :value="item.value"
                  >
                  </el-option>
                </el-select>
                <span>{{ errors[0] }}</span>
              </ValidationProvider>
            </el-form-item>

            <el-form-item
              label="URL Redirección"
              v-if="pageStore.formNode.type == 'redirect'"
            >
              <ValidationProvider
                name="URL Redirección"
                rules="required"
                v-slot="{ errors }"
              >
                <el-input v-model="pageStore.formNode.urlRedirect">
                  <template slot="prepend">URL:</template>
                </el-input>
                <span>{{ errors[0] }}</span>
              </ValidationProvider>
            </el-form-item>

            <el-form-item label="Target link">
              <ValidationProvider
                name="Target"
                rules="required"
                v-slot="{ errors }"
              >
                <el-select
                  v-model="pageStore.formNode.target"
                  filterable
                  placeholder="Seleccionar Target"
                >
                  <el-option
                    v-for="item in pageStore.targetOptions"
                    :key="item.value"
                    :label="item.label"
                    :value="item.value"
                  >
                  </el-option>
                </el-select>
                <span>{{ errors[0] }}</span>
              </ValidationProvider>
            </el-form-item>

            <el-form-item label="Visible menú">
              <el-switch v-model="pageStore.formNode.isVisible"></el-switch>
            </el-form-item>

            <el-form-item>
              <el-button
                @click.stop.prevent="onFormCancel"
                type="danger"
                icon="el-icon-back"
              >
                Cancelar</el-button
              >
              <el-button
                @click.stop.prevent="
                  !pageStore.isSubNode ? createNode() : createSubNode()
                "
                type="success"
                icon="el-icon-circle-plus-outline"
                :disabled="invalid"
                v-if="pageStore.formNode.isEdit"
              >
                Guardar</el-button
              >
              <el-button
                @click.stop.prevent="editNode"
                type="success"
                icon="el-icon-circle-plus-outline"
                :disabled="invalid"
                v-else
              >
                Actualizar</el-button
              >
            </el-form-item>
          </el-form>
        </ValidationObserver>
        <!-- End Form -->
      </div>
    </el-card>
  </div>
</template>

<script>
import { useStorePage } from '../../../stores/page/pageStore'
import { useStoreSite } from '../../../stores/siteStores'
import { useStoreConfig } from '../../../stores/configStore'
import { useStoreSiteMap } from '../../../stores/page/siteMapStore'
import _ from 'lodash'

const pageStore = useStorePage()
const siteStore = useStoreSite()
const configStore = useStoreConfig()
const siteMapStore = useStoreSiteMap()

export default {
  name: 'formNode',
  data () {
    return {
      pageStore,
      siteStore,
      configStore,
      formSiteMap: {},
      form: {},
      list: null,
      data: null
    }
  },
  created () {
    // this.list = pageStore.list
    // this.data = pageStore.data
    // this.formNode = pageStore.formNode
  },
  methods: {
    async createNode () {
      pageStore.loading = true
      if (pageStore.list.length === 0) {
        this.formSiteMap = _.cloneDeep(siteMapStore.templateSiteMap)

        await siteStore.showKeyName().then((response) => {
          this.formSiteMap.keyNameSite = _.cloneDeep(response.keyName)
        })

        this.formSiteMap.lang = _.cloneDeep(configStore.langSelected)
        await siteMapStore.storeSiteMap(this.formSiteMap)
      }

      delete pageStore.formNode.isEdit

      pageStore.list.push(pageStore.formNode)

      const map = _.cloneDeep(pageStore.list)
      const lang = _.cloneDeep(configStore.langSelected)
      await siteMapStore
        .updateSiteMap(map, lang)
        .then((response) => {
          pageStore.list = response.map

          pageStore.isFormVisible = false

          this.$router.push({
            name: 'pages.edit.content',
            params: { keyName: pageStore.formNode.keyNamePage }
          })
        })
        .finally(() => (pageStore.loading = false))
    },
    async createSubNode () {
      pageStore.loading = true
      const pageInternal = _.cloneDeep(pageStore.formNode)

      if (!pageStore.data.children) {
        this.$set(pageStore.data, 'children', [])
      }

      pageStore.data.children.push(pageInternal)

      const map = _.cloneDeep(pageStore.list)
      const lang = _.cloneDeep(configStore.langSelected)
      await siteMapStore
        .updateSiteMap(map, lang)
        .then((response) => {
          pageStore.list = response.map
          pageStore.isFormVisible = false

          // this.$router.push({
          //   name: 'pages.edit.content',
          //   params: { keyName: pageStore.data.keyNamePage }
          // })
        })
        .finally(() => (pageStore.loading = false))
    },
    async editNode () {
      pageStore.loading = true
      const idParent = pageStore.node.parent.id
      const idNodeEdit = pageStore.node.id

      if (idParent > 0) {
        pageStore.list.forEach((element) => {
          if (element.$treeNodeId === idParent) {
            element.children.forEach((element) => {
              if (element.$treeNodeId === idNodeEdit) {
                element = pageStore.formNode
              }
            })
          }
        })
      } else {
        pageStore.list.forEach((element) => {
          if (element.$treeNodeId === idNodeEdit) {
            element = pageStore.formNode
          }
        })
      }

      const map = _.cloneDeep(pageStore.list)
      const lang = _.cloneDeep(configStore.langSelected)
      await siteMapStore
        .updateSiteMap(map, lang)
        .then((response) => {
          pageStore.list = response.map

          pageStore.isFormVisible = false
        })
        .finally(() => (pageStore.loading = false))
    },

    onFormCancel () {
      pageStore.isFormVisible = false
    }
  }
}
</script>
